function AppBar(props) {
  const [active, setActive] = useState("");
  const history = useHistory();

  useEffect(() => {
    let unlisten = history.listen((location, action) => {
      //console.log(location);
      //console.log(action);

      if (location.pathname === "/dashboard") {
        setActive("dashboard");
      } else {
        setActive("");
      }
    });
    return () => {
      unlisten();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [active]);

  function logout(event) {
    event.preventDefault();
    localStorage.clear();
    sessionStorage.clear();
    history.replace("/login");
  }

  return (
    <nav
      id="appbar"
      className={
        "navbar sticky-top navbar-expand-lg navbar-dark shadow-sm " +
        (props.colorClass ? props.colorClass : "bg-primary")
      }
    >
      <div className="container">
        <Link className="navbar-brand" to="/" replace>
          <span className="h4 logo-text">VStore</span>
        </Link>

        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarNav">
          {localStorage.getItem(appConfig.KEY_TOKEN) && (
            <>
              <ul className="navbar-nav font-weight-medium">
                <li
                  className={
                    "nav-item" + (active === "dashboard" ? " active" : "")
                  }
                >
                  <Link
                    className="nav-link"
                    to="/dashboard"
                    data-toggle="collapse"
                    data-target=".navbar-collapse.show"
                    replace
                  >
                    Dashboard
                  </Link>
                </li>

                <li className="nav-item dropdown">
                  <a
                    href="!#"
                    className="nav-link dropdown-toggle"
                    id="bonusDropdown"
                    role="button"
                    data-toggle="dropdown"
                  >
                    Bonus
                  </a>
                  <div
                    className="dropdown-menu"
                    aria-labelledby="bonusDropdown"
                  >
                    <Link
                      className="dropdown-item"
                      to="/use-coupon-code"
                      data-toggle="collapse"
                      data-target=".navbar-collapse.show"
                    >
                      Use Coupon Code
                    </Link>
                    <Link
                      className="dropdown-item"
                      to="/redeem-voucher-code"
                      data-toggle="collapse"
                      data-target=".navbar-collapse.show"
                    >
                      Redeem Voucher
                    </Link>
                  </div>
                </li>
                <li className="nav-item dropdown">
                  <a
                    href="!#"
                    className="nav-link dropdown-toggle"
                    id="historyDropdown"
                    role="button"
                    data-toggle="dropdown"
                  >
                    History
                  </a>
                  <div
                    className="dropdown-menu"
                    aria-labelledby="historyDropdown"
                  >
                    <Link
                      className="dropdown-item"
                      to="/epin-history"
                      data-toggle="collapse"
                      data-target=".navbar-collapse.show"
                    >
                      E-Pin History
                    </Link>
                    <Link
                      className="dropdown-item"
                      to="/topup-history"
                      data-toggle="collapse"
                      data-target=".navbar-collapse.show"
                    >
                      Topup History
                    </Link>
                  </div>
                </li>
              </ul>
              <ul className="navbar-nav ml-auto font-weight-medium">
                <li className="nav-item">
                  {/* <Link to="/notifications" className="nav-link">
                      <FontAwesomeIcon icon={["fas", "bell"]} size="lg" />
                      <NavNotification />
                    </Link> */}
                  <a href="!#" className="nav-link" onClick={logout}>
                    Logout
                  </a>
                </li>
                <li className="nav-item dropdown d-none">
                  <Link to="/profile" className="nav-link d-block d-lg-none">
                    <FontAwesomeIcon icon={["fas", "bars"]} size="lg" />
                  </Link>
                  <a
                    href="!#"
                    className="nav-link dropdown-toggle d-none"
                    id="navbarDropdown"
                    role="button"
                    data-toggle="dropdown"
                  >
                    <FontAwesomeIcon icon={["fas", "user-circle"]} size="lg" />
                  </a>
                  <div
                    className="dropdown-menu dropdown-menu-right"
                    aria-labelledby="navbarDropdown"
                  >
                    <Link className="dropdown-item" to="/">
                      Change Name
                    </Link>
                    <Link className="dropdown-item" to="/">
                      Change Password
                    </Link>
                    <div className="dropdown-divider"></div>
                    <a className="dropdown-item" href="!#">
                      Logout
                    </a>
                  </div>
                </li>
              </ul>
            </>
          )}
        </div>
      </div>
    </nav>
  );
}

export default AppBar;
